<template>
    <v-container>
        <v-layout v-if="error">
            <app-alert
            @dismissed="onDismissed"
            :text="error.message"
            >
            </app-alert>
        </v-layout>

        <v-layout>
            <v-flex>
                <v-card>
                    <v-card-text>
                        <v-container>
                            <form @submit.prevent="onSignup">

                                <v-layout>
                                    <v-flex>
                                        <v-text-field
                                        name="email"
                                        label="Email"
                                        id="email"
                                        v-model="email"
                                        type="email"
                                        required>    
                                        </v-text-field>
                                    </v-flex>
                                </v-layout>

                                <v-layout>
                                    <v-flex>
                                        <v-text-field
                                        name="password"
                                        label="Password"
                                        id="password"
                                        v-model="password"
                                        type="password"
                                        required>    
                                        </v-text-field>
                                    </v-flex>
                                </v-layout>

                                <v-layout>
                                    <v-flex>
                                        <v-text-field
                                        name="confirmPassword"
                                        label="Confirm Password"
                                        id="confirmPassword"
                                        v-model="confirmPassword"
                                        type="password"
                                        :rules="[comparePasswords]">  </v-text-field>
                                    </v-flex>
                                </v-layout>

                                <v-layout>
                                    <v-btn type="submit" :disabled="loading" :loading="loading">Sign Up
                                        <span slot="loader" class="custom-loader">
                                            <v-icon light>cached</v-icon>
                                        </span>
                                    </v-btn> 
                                </v-layout>                               

                            </form>
                        </v-container>
                    </v-card-text>
                </v-card>
            </v-flex>
        </v-layout>
    </v-container>
</template>

<script>
export default {
    data () {
        return {
            email: '',
            password: '',
            confirmPassword: ''
        }
    },
    computed: {
        comparePasswords () {
            return this.password !== this.confirmPassword ? 'Passwords do not match' : ''
        },
        user () {
            return this.$store.getters.user
        },
        error () {
            return this.$store.getters.error
        },
        loading () {
            return this.$store.getters.loading
        }
    },
    watch: {
        user (value) {
            if (value !== null && value !== undefined) {
                this.$router.push('/')
            }
        }
    },
    methods: {
        onSignup () {
            //Using VueX to store the state/information
            this.$store.dispatch('signUserUp', {email: this.email, password: this.password})
        },
        onDimissed () {
            console.log('Dismissed Alert!')
            this.$store.dispatch('clearError')
        }
    }
}
</script>